import React from "react";
import axios from 'axios';
import swal from 'sweetalert';
import "antd/dist/antd.css";
import "../../container/index.css";
import { Link, Switch, Route } from 'react-router-dom';
import BasicDetails from './BasicDetails';
import ContactDetails from './ContactDetails';
import {
  Form,
  Icon,
  Card,
  Menu
} from "antd";

class Details extends React.Component {
  state = {
    confirmDirty: false,
    basic: false,
    contact: false,
    educational: false,
    Experions: false
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        
        const favorite = {
            user_id: localStorage.getItem("id"),
            user_name: localStorage.getItem("user"),
            food: values.food || undefined,
            drink: values.drink || undefined,
            animal: values.animal || undefined,
            bird: values.bird || undefined,
            hobby: values.hobby || undefined,
            place: values.place || undefined
        }
        axios.post('http://localhost:5000/savefavo', favorite)
        .then(res => {
            console.log("res", res.data);
            swal("OMG!","User Favorite Submited!!!","success");
        }).catch(e => console.log("eeeeeeeeeeee",e));
        this.props.history.push('/read'); 
      }
    });
  };



  render() {
    

    return (
      <div>
      
      <Menu onClick={this.handleClick} defaultSelectedKeys={['1']} mode="horizontal">
        <Menu.Item key="1">
          <Link to='/add_employee/basic'/>
          <Icon type="mail" />
          Basic Details
        </Menu.Item>
        <Menu.Item key="2" >
          <Link to='/add_employee/contact'/>
          <Icon type="appstore" />
          Contact Details
        </Menu.Item>
        <Menu.Item key="3" >
          <Link to='/add_employee/educational'/>
          <Icon type="appstore" />
          Educational Details
        </Menu.Item>
        <Menu.Item key="4" >
          <Link to='/add_employee/experions'/>
          <Icon type="appstore" />
          Experions Details
        </Menu.Item>
      </Menu> 
      
      <div className="scrollable-container-fav" style={{width:'800px', float:'left'}}>       
      <div className="background-fav">
      <Card bordered={false} style={{ width: 780, height: '600px'}}>
      <Switch>
        <Route path='/add_employee/basic' component={BasicDetails} />
        <Route path='/add_employee/contact' component={ContactDetails} />
      </Switch>
      </Card>
      </div>
      </div>
      
      <div style={{float: 'right', height: '550px', paddingTop: '0px', paddingLeft: '0px'}}>
        <img height= '530px' width= '475px' alt="example" src="https://packagetoursturkey.com/wp-content/uploads/2018/06/beach-calm-clouds-457882.jpg" />
      </div> 
      </div>
    );
  }
}

const WrappedRegistrationForm = Form.create({ name: "favorite" })(
  Details
);


export default WrappedRegistrationForm;

